import React from "react"
import clamp from 'lodash-es/clamp'
import {useSpring, animated, interpolate} from 'react-spring'
import { useGesture, useDrag } from 'react-with-gesture'
import "./Slider.css"


function Slider({
    index,
    offsetRadius,
    gameName,
    content,
    icon,
    link,
    moveSlide,
    delta,
    down,
    animationConfig,
}){ 
    const [{ xy }, set] = useSpring(() => ({ xy: [0, 0] }))
    const bind = useGesture(({ down, delta, velocity }) => {
      velocity = clamp(velocity, 1, 8)
      set({ xy: down ? delta : [0, 0], config: { mass: velocity, tension: 500 * velocity, friction: 50 } })
      
      if(xy.getValue()[1] < -200 && down)
      {
          moveSlide(-1);
      }

      console.log(xy.getValue()[1]);
    })

    let translateY = 0;

    const offsetFromMiddle = index - offsetRadius;
    const totalPresentables = 2 * offsetRadius + 1;
    const distanceFactor = 1 - Math.abs(offsetFromMiddle / (offsetRadius + 1));

    const binds = useSpring({
        to:{
            transform:`translate(-50%, -50%) scale(${distanceFactor})`,
            zIndex: Math.abs(Math.abs(offsetFromMiddle) - 2),
            top: `${
              offsetRadius === 0 ? 50 : (offsetFromMiddle * 50) / offsetRadius
            }%`,
          },
        config: {animationConfig},
    })

    return(

        <animated.div className="SliderContainer" 
            style={ binds }>
            <animated.div className="SliderCard" {...bind()} style=
            {{transform: xy.interpolate((x,y) => `translate3d(0,${y}px,0)`), 
                opacity: distanceFactor * distanceFactor}}>

                <a className="gameName">{gameName}</a>
                <a className="context">{content}</a>
                <a className="icon" href={link}>
                <img className="img" src={icon} ></img>
                </a>
            </animated.div>
        </animated.div>
    );
}


export default Slider;